import React, {Component} from "react";
import {Link} from 'react-router-dom';
import {FiX, FiMenu} from "react-icons/fi";
import { useState } from "react";
import { Widget3DTest } from '../../3d/widget3D';


class Header extends Component {

  /**
   * isXrSupported
   * 
   * @returns {Boolean}
   */
    isXrSupported (){
        let supported = false;
        if('xr' in navigator)
            supported = navigator.xr.isSessionSupported('immersive-vr');
        return supported;
    }

    renderVRButton (){  
        const textButton = this.isXrSupported() ? "GoToViaR" : "GoTo3D";

        if (this.state.vrMode === false)
        {
            return (
                <div className="header-btn" >
                    <a className="btn-default btn-border btn-opacity color-gtvr" href="#">
                    <span onClick = { () =>this.setvrMode(true) }>{textButton}</span>
                </a>
            </div>)
        }
        else
        {
            return <Widget3DTest/>
        }
    }

    constructor(props) {
        super(props);
        this.menuTrigger = this.menuTrigger.bind(this);
        this.CLoseMenuTrigger = this.CLoseMenuTrigger.bind(this);
        //  this.subMetuTrigger = this.subMetuTrigger.bind(this);
        window.addEventListener('load', function () {
            console.log('All assets are loaded')
        })
        //const [vrMode, setvrMode] = useState(false);
        this.state = {
            vrMode:false
        };
    }

    setvrMode(){
        this.setState({vrMode: !this.state.vrMode});
    }

    menuTrigger() {
        document.querySelector('.header-wrapper').classList.toggle('menu-open')
    }

    CLoseMenuTrigger() {
        document.querySelector('.header-wrapper').classList.remove('menu-open');
    }

    render() {
        const {menuItems = []} = this.props

        var elements = document.querySelectorAll('.has-droupdown > a');
        for (var i in elements) {
            if (elements.hasOwnProperty(i) && this.parentElement) {
                elements[i].onclick = function () {
                    this.parentElement.querySelector('.submenu').classList.toggle("active");
                    this.classList.toggle("open");
                }
            }
        }

        const {logo, color = 'default-color'} = this.props;
        let logoUrl;
        if (logo === 'light') {
            logoUrl = <img src="/assets/images/logo/logo-light.png" alt="Digital Agency"/>;
        } else if (logo === 'dark') {
            logoUrl = <img src="/assets/images/logo/logo-dark.png" alt="Digital Agency"/>;
        } else if (logo === 'symbol-dark') {
            logoUrl = <img src="/assets/images/logo/logo-symbol-dark.png" alt="Digital Agency"/>;
        } else if (logo === 'symbol-light') {
            logoUrl = <img src="/assets/images/logo/logo-symbol-light.png" alt="Digital Agency"/>;
        } else {
            logoUrl = <img src="/assets/images/logo/logo.png" alt="Digital Agency"/>;
        }

        logoUrl = <img src="/assets/images/logo/logo_wit_gotoviar_step_inside.png" alt="Software" />;

        return (
            <header className={`header-area header--fixed formobile-menu header--transparent ${color}`}>
                <div className="header-wrapper" id="header-wrapper">
                    <div className="header-left">
                        <div className="logo">
                            <a href="/">
                                {logoUrl}
                            </a>
                        </div>
                    </div>

                    <div className="header-right">
                        {/*
                        <nav className="mainmenunav d-lg-block">
                            <ul className="mainmenu">
                                {
                                    menuItems.map(({id, label, subMenu}) => {
                                        return (
                                            <li key={id + label} className="has-droupdown">
                                                {!!subMenu ? <a href='#'>{label}</a> : (<Link to={id}>
                                                    {label}
                                                </Link>)}

                                                {subMenu && (
                                                    <ul className="submenu">
                                                        {subMenu.map(subMenuItem => {
                                                            return (
                                                                <li key={subMenuItem.id + subMenuItem.id}>
                                                                    <Link to={subMenuItem.id}>
                                                                        {subMenuItem.label}
                                                                    </Link>
                                                                </li>
                                                            )
                                                        })}
                                                    </ul>
                                                )}
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                        </nav>
                        */}

                        { this.renderVRButton() }

                        {/* Start Humberger Menu  */}
                        <div className="humberger-menu d-block d-lg-none pl--20 pl_sm--10">
                            <span onClick={this.menuTrigger} className="menutrigger text-white"><FiMenu/></span>
                        </div>
                        {/* End Humberger Menu  */}
                        <div className="close-menu d-block d-lg-none">
                            <span onClick={this.CLoseMenuTrigger} className="closeTrigger"><FiX/></span>
                        </div>
                    </div>
                </div>

            </header>
        )
    }
}

export default Header;